    margin-left: 7px;
    cursor: pointer;
  
  }
  
  .post__option:hover{
    background-color: #dcdee0af;
    border-radius: 8px;
  }
  
  .post__firstSectionModal {
    display: flex;
    flex-direction: row;
    background-color: #F5F6F7;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    margin-bottom: 10px;
  }
  
  
  
  
  
  .post__avatar {
    margin-left: 10px;
    margin-right: 10px;
  }
  
  .containerr {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 12px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border-radius: 40px;
  }
  
  /* Hide the browser's default checkbox */
  .containerr > input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    border-radius: 40px;
  }
  
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    margin-top: 20px;
    margin-left: 10px;
    background-color: #eee;
    border: 1px solid gray;
    border-radius: 20px;
  }
  
  
  .containerr:hover input ~ .checkmark {
    background-color: #ccc;
  }
  
  
  .containerr input:checked ~ .checkmark {
    background-color: #4267B2;
    border-radius: 20px;
    border: none;
  }
  
  
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  
  .containerr input:checked ~ .checkmark:after {
    display: block;
  }
  
  
  .containerr .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    transform: rotate(45deg);
  }
  
  
  .imageupload__submitButton {
    width: 95%;
    margin-left: 10px !important;
    margin-right: 10px !important;
    margin-bottom: 10px !important;
    background-color:#085178ea!important;
    color: #fff !important;
    border-radius: 0px !important;
    height: 30px !important;
  }
    
  
  .progress {
    width: 90%;
    margin-right: 10px;
    margin-left: 20px;
    margin-bottom:10px;
   
  }
  
  .progress.show {
  width: 90%;
  margin-right: 10px;
  margin-left: 20px;
  margin-bottom:10px;
  }
  
  
  
  .post__top > input {
    background-color: transparent;
    border: none;
    width:100%;
    outline: none;
  }
  
  .post__top > input::-webkit-input-placeholder {
   
